<template>
    <div id="app">
        <transition name='fade'>
            <img-viewer v-if='show' :url="url" @toggle="toggleImgViewer(url)"/>
        </transition>
        <index-header />
        <router-view />
        <index-footer />
    </div>
</template>

<script>
import IndexHeader from './components/Header.vue'
import IndexFooter from './components/Footer.vue'
import ImgViewer from './components/utils/ImgViewer.vue'

import Bus from './bus/index.js'

export default {
    name: "App",
    data() {
        return {
            show: false,
            url: ""
        }
    },
    components: {
        IndexHeader, IndexFooter, ImgViewer
    },
    created() {
        var that = this
        Bus.$on('toggle', url => {
            that.toggleImgViewer(url)
        })
    },
    methods: {
        toggleImgViewer: function(url) {
            this.show = !this.show
            this.url = url
        }
    }
};
</script>

<style>
    body {
        margin: 0;
    }
    #app {
        font-family: "Avenir", Helvetica, Arial, sans-serif;
    }
    a {
        color: #000;
        text-decoration: underline;
    }
    a:hover {
        color: #000;
        text-decoration: underline dotted #000;
    }
    .fade-enter-active, .fade-leave-active {
        transition: opacity 0.5s ease;
    }
    .fade-enter, .fade-leave-to {
        opacity: 0;
    }
</style>



