import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
    routes: [
        {
            path: '/',
            name: 'Home',
            component: () => import ("@/components/Home/Home.vue"),
            meta: {
                title: 'Wayne\'s'
            }
        }, {
            path: '/Blog',
            name: 'Blog',
            component: () => import ("@/components/Blog/Blog.vue"),
            meta: {
                title: 'Wayne\'s Blog',
            }
        }, {
            path: '/Work',
            name: 'Work',
            component: () => import ("@/components/Work/Work.vue"),
            meta: {
                title: 'Wayne\'s Work',
            }
        }, {
            path: '/Me',
            name: 'Me',
            component: () => import ("@/components/Me/Me.vue"),
            meta: {
                title: 'Wayne\'s Work',
            }
        }, {
            path: '/Blog/:blogId',
            component: () => import ("@/components/Blog/BlogContent.vue")
        }
    ],
    mode: "history"
})



