<template>
    <div class="img-viewer">
        <div class="_background" @click="exit">
            <div class="container">
                <div class="row">
                    <div class="col-lg-8 offset-lg-2">
                        <div class="_image-viewer" @click="none">
                            <img :src="this.url">
                        </div>
                    </div>
                </div>
            </div>
            <div class="_exit-btn" @click="exit">关闭</div>
        </div>
    </div>
</template>

<script>
import Bus from "../../bus/index.js"

export default {
    name: "img-viewer",
    data() {
        return {

        }
    },
    props: {
        url: String
    },
    methods: {
        exit: function(e) {
            Bus.$emit('toggle')
            this.none(e)
        },
        none: function(e) {
            e.stopPropagation()
        }
    }
}
</script>

<style scoped>
    .img-viewer {
        width: 100%;
    }
    ._background {
        display: flex;
        align-items: center;
        position: fixed;
        width: 100%;
        height: 100vh;
        background: rgba(0, 0, 0, 0.8);
        top: 0;
        left: 0;
        z-index: 10;
    }
    ._image-viewer {
        background: #fff;
        padding: 10px;
        aspect-ratio: 16 / 9;
    }
    ._image-viewer img {
        user-select: none;
        display: block;
        margin: auto;
        max-width: 100%;
        max-height: 100%;
    }
    ._exit-btn {
        position: absolute;
        top: 10px;
        right: 10px;
        padding: 4px 8px;
        text-decoration: underline;
        background: #fff;
        user-select: none;
        cursor: pointer;
        border: 1px solid #000;
    }
</style>


