<template>
    <div>
        <div class="header">
            <div class="navigation">
                <ul>
                    <!-- <li v-for="(url, index) of $router.options.routes" :key="index"><router-link :to='url.path'>{{url.name}}</router-link></li> -->
                    <li><router-link to="/">主页</router-link></li>
                    <li><router-link to="/Blog">博客</router-link></li>
                    <li><router-link to="/Work">作品</router-link></li>
                    <li><router-link to="/Me">我</router-link></li>
                </ul>
            </div>
            <!-- <div class="navigation-mobile d-lg-none d-block">
                <ul>
                    <li><router-link to='/'>Home</router-link></li>
                    <li><router-link to='Blog'>Blog</router-link></li>
                    <li><router-link to='Work'>Work</router-link></li>
                    <li><router-link to='Me'>Me</router-link></li>
                </ul>
            </div> -->
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            
        }
    },
    mounted() {
        
    }
}
</script>

<style scoped>
    a {
        text-decoration: none;
    }
    .header {
        background: #000;
    }
    .navigation ul, .navigation-mobile ul {
        margin: 0 auto;
        padding: 0;
        list-style: none;
        text-transform: uppercase;
        font-size: 0;
        width: fit-content;
    }
    .navigation ul li, .navigation-mobile ul li {
        font-size: 1rem;
        display: inline-block;
        padding: 30px 20px;
    }
    .navigation ul li a, .navigation-mobile ul li a {
        position: relative;
        color: #fff;
    }
    .navigation ul li a::after {
        content: '';
        transition: ease 0.5s;
        display: block;
        position: absolute;
        transform: rotateY(90deg);
        height: 1px;
        width: 100%;
        background: #fff;
    }
    .navigation ul li:hover a::after {
        transform: rotateY(0);
    }
    .navigation-mobile ul {
        margin: 0 auto;
        padding: 0;
        list-style: none;
        text-transform: uppercase;
        font-size: 0;
        width: fit-content;
    }
</style>


